.Dish {
  border-bottom: 0.3rem solid var(--color-primary);
  border-radius: 0.4rem;
  border-top: 0.3rem solid var(--color-primary);
  margin-bottom: 3rem;
}

.Dish__header {
  background-color: var(--color-white);
  border-bottom: 0.1rem solid var(--color-black);
  padding: 0.5rem 0;
  font-size: 1.6rem;
}

.Dish__name {
  font-weight: bold;
  font-size: 120%;
  line-height: 1.5;
  margin: 0;
  text-align: center;
}

.Dish__subtitle {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 70%;
  font-weight: 300;
  margin: 0;
  text-align: center;
}

.Dish__body {
  background-color: var(--color-white);
  border-bottom: 0.1rem solid var(--color-black);
}

.Dish__component {
  font-size: 1.3rem;
  line-height: 1.5;
  margin: 0;
  text-align: center;
}

.Dish__component--primary {
  font-weight: bold;
}

.Dish__footer {
  background-color: hsla(0, 0%, 100%, 0.9);
}

.Dish__link-wrapper {
  text-align: center;
  margin: 0;
}

.Dish__price {
  font-size: 1.4rem;
  margin: 0;
  text-align: center;
}



