:root {
  --color-primary: #FFC300;
  --color-secondary: #DD1021;
  --color-success: #3ADB76;
  --color-warning: #FFAE00;
  --color-alert: #CC4B37;
  --color-white: #FEFEFE;
  --color-light-gray: #E6E6E6;
  --color-medium-gray: #CACACA;
  --color-dark-gray: #8A8A8A;
  --color-black: #0A0A0A;
}

body {
  background-image: url(https://preview.ibb.co/mtnbT5/Whats_App_Image_2017_05_14_at_18_18_42.jpg);
  background-attachment: fixed;
  background-size: cover;
  font-family: 'Roboto Slab', sans-serif;
  margin: 1rem;
  color: var(--color-black);
}



